.sc-sanpham-cate .title-cate{font-family:'brandontext_bold', sans-serif;font-size:18px;border-bottom:1px solid #ccc;margin:0;margin-bottom:15px}.sc-sanpham-cate .title-cate a{text-transform:uppercase;display:inline-block;text-decoration:none;padding:5px 10px 10px 0;border-bottom:1px solid #00a651;margin-bottom:-1px}.sc-sanpham-cate .title-cate .filter-view{font-family:'brandontext_regular', sans-serif}.sc-sanpham-cate .title-cate .filter-view label{font-size:14px;font-weight:700;color:#888;padding-right:15px}.sc-sanpham-cate .title-cate .filter-view select{font-size:14px;font-weight:normal;padding:5px 10px;border:1px solid #f3f3f3}.sc-sanpham-cate .title-cate .filter-view [class^='view-']{width:30px;height:30px;border:none;float:right;padding:0;margin:0 2px}.sc-sanpham-cate .title-cate .filter-view [class^='view-'].active{background-color:#00a651 !important;background-position:bottom center}.sc-sanpham-cate .title-cate .filter-view .view-grid{background:#fdfdfd url("../img/grid-view-bg.png") top center no-repeat}.sc-sanpham-cate .title-cate .filter-view .view-list{background:#fdfdfd url("../img/list-view-bg.png") top center no-repeat}.sc-sanpham-cate .prod-cate{margin-bottom:20px}.sc-sanpham-cate .prod-cate h3{font-size:14px;text-align:center}.sc-sanpham-cate .prod-cate h3 a{color:#333}.sc-sanpham-cate .prod-cate h3 a:hover{color:#00a651}
